@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: "Noto Sans JP", sans-serif;
}

body {
  margin: 0;
}

.app header h1 {
  margin: 0 0 20px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgb(230, 115, 8);
}
.app header h1 .fas {
  color: rgb(11, 138, 0);
  font-size: 50px;
  margin: 0 8px;
}

.app {
  margin: 2%;
  font-family: "Courier New", Courier, monospace;
}

.app .user__form {
  display: flex;
  align-items: center;
  justify-content: center;
}
.app .todo_list {
  margin-top: 10px;
}

.app footer {
  background-color: rgb(206, 255, 255);
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  z-index: 99;
  position: fixed;
  width: 100%;
  left: 0;
  right: 0;
  bottom: 0;
}

@media screen and (max-width: 576px) {
  .app footer {
    font-size: 12px;
    font-weight: 600;
  }
  .app header h1 {
    font-size: 25px;
  }
  .app header h1 .fas {
    color: rgb(11, 138, 0);
    font-size: 30px;
    margin: 0 8px;
  }
}

.card {
  box-shadow: 0 0 1rem 0 rgba(0, 0, 0, 0.5);
  border-radius: 5px;
  padding: 5px;
}

.user__form {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2%;
}
.user__form input {
  font-weight: 900;
  font-size: 20px;
  padding: 10px;
  margin: 0 9px;
  border: none;
  border-radius: 5px;
  box-shadow: 0 0 1rem 0 rgba(4, 75, 1, 0.644);
}
.user__form input[type="text"] {
  outline: 0;
  box-shadow: 5px 5px 10px 5px rgba(4, 75, 1, 0.644);
  transition: all 0.5s ease-in-out;
}
.user__form input.invalid[type="text"] {
  background-color: rgb(231, 42, 42);
  color: black;
  box-shadow: 5px 5px 10px 5px rgba(241, 1, 1, 0.644);
}
.user__form input[type="text"]:focus {
  background-color: rgba(255, 255, 255, 0.692);
  color: rgb(0, 0, 0);
}

.user__form input[type="submit"] {
  background-color: rgb(14, 112, 14);
  color: white;
  cursor: pointer;
}
.user__form input[type="submit"]:hover {
  background-color: rgb(190, 82, 19);
}

@media screen and (max-width: 600px) {
  .user__form input[type="text"] {
    width: 80%;
  }
  .user__form input {
    font-size: 16px;
  }
}

.todo_item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  margin: 5px;
}
.todo_item h3 {
  box-shadow: 0 0 1rem 0 rgba(0, 0, 0, 0.644);
  padding: 20px;
  width: 50%;
  font-size: 18px;
  border-radius: 5px;
  transition: all 0.5s ease-in-out;
}
.todo_item h3.complete {
  text-decoration: line-through;
  box-shadow: 5px 5px 10px 5px rgba(4, 75, 1, 0.644);
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  text-decoration-thickness: 2px;
  -webkit-text-decoration-color: green;
          text-decoration-color: green;
}
.todo_item .task_btn div {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  font-size: 22px;
  cursor: pointer;
  color: white;
  margin: 5px;
  box-shadow: 0 0 1rem 0 rgba(0, 0, 0, 0.644);
  border-radius: 5px;
  transition: -webkit-transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}
.todo_item .task_btn div:hover {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

.todo_item .task_btn .done {
  background-color: rgb(14, 112, 14);
  height: 100%;
}
.todo_item .task_btn .delete {
  background-color: rgb(190, 82, 19);
  height: 100%;
}

@media screen and (max-width: 600px) {
  .todo_item .task_btn div {
    padding: 8px;
    font-size: 16px;
  }
  .todo_item h3 {
    padding: 10px;
    font-size: 16px;
  }
  .todo_item {
    padding: 0 10px;
    margin: 4px;
  }
}

h4 {
  text-align: center;
}

